@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
.cart-icon-container{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon-container .cart-icon{width:24px;height:24px}.cart-icon-container #item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.custom-button{min-width:165px;width:auto;height:50px;display:flex;justify-content:center;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-log-in{background-color:#4285F4;color:white}.custom-button.google-log-in:hover{background-color:#357AE8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart .empty-cart-message{margin:50px auto;font-size:18px}.cart .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll;overflow-x:hidden}.cart button{margin-top:auto}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden;background-position:center;background-size:cover}.menu-item.large{height:380px}@media screen and (max-width: 800px){.menu-item.large{height:200px}}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}@media screen and (max-width: 800px){.menu-item.large{height:200px}}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}@media screen and (max-width: 800px){.menu-item{height:200px}}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.collections-overview{display:flex;flex-direction:column}

.collection-item{position:relative;width:22vw;display:flex;flex-direction:column;height:350px;align-items:center}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{position:absolute;top:255px;width:80%;display:none;opacity:0.7}@media screen and (max-width: 800px){.collection-item .custom-button{padding:0 10px;min-width:unset;display:block;opacity:0.9}}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{display:flex;opacity:0.85}.collection-item .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}@media screen and (max-width: 800px){.collection-item{width:40vw}.collection-item:hover .image{opacity:unset}.collection-item:hover .custom-button{opacity:unset}}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}@media screen and (max-width: 800px){.collection-preview .preview{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px}}@media screen and (max-width: 800px){.collection-preview{align-items:center}}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items>div{margin-bottom:30px}@media screen and (max-width: 800px){.collection-page .items{grid-template-columns:1fr 1fr;grid-gap:15px}}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.log-in{width:380px;display:flex;flex-direction:column}.log-in .title{margin:10px 0}.log-in .buttons{display:flex;justify-content:space-between}@media screen and (max-width: 800px){.log-in{width:100%}.log-in .buttons{justify-content:center}.log-in .buttons .custom-button{transform:scale(0.9)}.log-in .buttons .custom-button:last-of-type{font-size:11.8px}}

.register{width:380px;display:flex;flex-direction:column}.register .title{margin:10px 0}.register .buttons{display:flex;justify-content:center}@media screen and (max-width: 800px){.register{width:100%}}

.auth-page{margin:30px auto 0;width:850px;display:flex;justify-content:space-between}@media screen and (max-width: 800px){.auth-page{width:90%;align-items:center;flex-direction:column}.auth-page>*:first-child{margin-bottom:50px}}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .quantity-value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}@media screen and (max-width: 800px){.checkout-item{font-size:18px}}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}@media screen and (max-width: 800px){.checkout-page .checkout-header .header-block:last-child{width:12%}}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}.checkout-page button{margin-top:50px;margin-left:auto}.checkout-page .test-warning{margin-top:40px;text-align:center;font-size:24px;color:red}@media screen and (max-width: 800px){.checkout-page{width:90%}}

* {
	box-sizing: border-box;
}

body {
	padding: 20px 60px;
	font-family: 'Open Sans Condensed', sans-serif;
}

a {
	color: black;
	text-decoration: none;
}

@media screen and (max-width: 800px) {
	body {
		padding: 10px;
	}
}
body {
	margin: 0;
	font-family: 'Open Sans Condensed', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

