.checkout-page {
	width: 55%;
	min-height: 90vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 50px auto 0;

	.checkout-header {
		width: 100%;
		padding: 10px 0;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid darkgrey;

		.header-block {
			text-transform: capitalize;
			width: 23%;

			&:last-child {
				width: 8%;
			}

			@media screen and (max-width: 800px) {
				// width: 22%;

				&:last-child {
					width: 12%;
				}
			}
		}
	}

	.total {
		margin-top: 30px;
		margin-left: auto;
		font-size: 36px;
	}

	button {
		margin-top: 50px;
		margin-left: auto;
	}

	.test-warning {
		margin-top: 40px;
		text-align: center;
		font-size: 24px;
		color: red;
	}

	@media screen and (max-width: 800px) {
		width: 90%;
	}
}